<template>
  <div id="product-details">
    <div class="d-flex justify-content-between align-items-center">
      <h3>Product Detail</h3>
      <div class="d-flex">
        <button class="btn text-danger" @click="deleteProduct">Delete</button>
        <router-link
          v-bind:to="{ path: '/products/' + id + '/edit' }"
          class="btn btn-success"
        >
          Edit Product
        </router-link>
      </div>
    </div>
    <ul class="list-group my-3">
      <h1>General Information</h1>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">ID</span>
          <span class="col-12 col-md-6">{{ id }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Name</span>
          <span class="col-12 col-md-6">{{ name }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Type</span>
          <span class="col-12 col-md-6">{{ type }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Orders</span>
          <span class="col-12 col-md-6">{{ orders }}</span>
        </div>
      </li>
      <h1>Price Information</h1>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Wholesale</span>
          <span class="col-12 col-md-6">{{ price.wholesale }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Retail</span>
          <span class="col-12 col-md-6">{{ price.retail }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Distribution</span>
          <span class="col-12 col-md-6">{{ price.distribution }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold">Agent</span>
          <span class="col-12 col-md-6">{{ price.agent }}</span>
        </div>
      </li>
      <li class="list-group-item">
        <div class="row">
          <span class="col-12 col-md-6 font-weight-bold"
            >Smallest specifications</span
          >
          <span class="col-12 col-md-6">{{ price.smallestSpec }}</span>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { db } from "../../configs/firebaseInit";
export default {
  name: "product-details",
  data() {
    return {
      id: null,
      name: null,
      type: null,
      orders: null,
      price: {
        wholesale: 0,
        retail: 0,
        distribution: 0,
        agent: 0,
        smallestSpec: 0,
      },
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("products")
      .where("id", "==", to.params.id)
      .get()
      .then((qureySnapshot) => {
        qureySnapshot.forEach((doc) => {
          next((vm) => {
            vm.id = doc.data().id;
            vm.name = doc.data().name;
            vm.type = doc.data().type;
            vm.orders = doc.data().orders;
            vm.price.wholesale = doc.data().price
              ? doc.data().price.wholesale
              : 0;
            vm.price.retail = doc.data().price ? doc.data().price.retail : 0;
            vm.price.distribution = doc.data().price
              ? doc.data().price.distribution
              : 0;
            vm.price.agent = doc.data().price ? doc.data().price.agent : 0;
            vm.price.smallestSpec = doc.data().price
              ? doc.data().price.smallestSpec
              : 0;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("products")
        .where("id", "==", this.$route.params.id)
        .get()
        .then((qureySnapshot) => {
          qureySnapshot.forEach((doc) => {
            this.id = doc.data().id;
            this.name = doc.data().name;
            this.type = doc.data().type;
            this.orders = doc.data().orders;
            this.price.wholesale = doc.data().price.wholesale;
            this.price.retail = doc.data().price.retail;
            this.price.distribution = doc.data().price.distribution;
            this.price.agent = doc.data().price.agent;
            this.price.smallestSpec = doc.data().price.smallestSpec;
          });
        });
    },
    deleteProduct() {
      if (confirm("Are you sure to delete product " + this.id)) {
        db.collection("products")
          .where("id", "==", this.id)
          .get()
          .then((qureySnapshot) => {
            qureySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push("/products");
            });
          });
      }
    },
  },
};
</script>
